.surgery-results {
    position: relative;
    z-index: 2;
    padding: 0 1.5rem;
    &__wrap {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
    }
    &__header {
        position: relative;
    }
    &__h2 {
        color: $header-color;
        font-family: $regular;
        letter-spacing: 0.5px;
        font-weight: 400;
        line-height: 28px;
        padding-left: 3.3rem;
        font-size: 1.8rem;
        text-transform: uppercase;
    }
    &__pointer.pointer__wrap {
        left: 0;
    }
    &__list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 1.8rem 0;
    }
    &__item {
        padding: 0.5rem 0;
    }
    &__link {
        display: flex;
        justify-content: center;
        align-items: center;
        width: calc(50vw - 2.75rem);
        height: calc(50vw - 2.75rem);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            background-image: $small-img-grad;
            z-index: 3;
        }
    }
    &__link-text {
        position: relative;
        text-align: center;
        z-index: 4;
        color: $white-1;
        font-family: $light;
        font-size: 1.8rem;
        font-weight: 300;
        text-transform: uppercase;
        letter-spacing: 0.5px;
    }
    &__right {
        margin: 0 -1.5rem;
    }
    &__wrap-slider {
        height: 92.5vw;
        max-height: 92.5vw;
        width: 100%;
    }
    &__slider {
        position: relative;
        overflow: hidden;
        .slick-dots {
            position: absolute;
            display: flex;
            flex-direction: row;
            justify-content: center;
            width: 100%;
            bottom: 2rem;
            list-style: none;
            li {
                width: 0.8rem;
                height: 1.2rem;
                display: flex;
                align-items: center;
                margin: 0 0.45rem;
            }
            [type='button'] {
                outline: none;
                width: 0.8rem;
                height: 0.8rem;
                border: none;
                background-color: $white-04;
                overflow: hidden;
                color: transparent;
            }
            li.slick-active {
                width: 1.2rem;
                height: 1.2rem;
                [type='button'] {
                    width: 1.2rem;
                    height: 1.2rem;
                    background-color: $white-08;
                }
            }
        }
    }
    &__before,
    &__after {
        height: 50%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: $slider-overlay;
        }
    }
}

@media screen and (min-width: 768px) {
    .surgery-results {
        padding: 0 2.5rem 3rem;
        &__pointer.pointer__wrap {
            left: 2rem;
        }
        &__h2 {
            padding-left: 6rem;
            font-size: 2rem;
            letter-spacing: 0.56px;
            line-height: 28px;
        }
        &__item {
            padding: 0.75rem 0;
        }
        &__link {
            width: calc(33vw - 3rem);
            height: calc(33vw - 3rem);
        }
        &__link-text {
            font-size: 2rem;
            letter-spacing: 0.56px;
        }
        &__right {
            margin: 0 -2.5rem;
        }

        &__slider {
            .slick-dots {
                li {
                    margin: 0 1rem;
                }
                
            }
        }
    }
}

@media screen and (min-width: 1024px) {
    .surgery-results {
        &__right {
            max-height: 45rem;
        }
        &__slider {
            max-height: 45rem;
            &.slick-slider.slick-vertical {
                .slick-slide {
                    max-height: 45rem;
                }
            }
            &.slick-initialized .slick-slide {
                display: flex;
                flex-direction: row;
            }
        }
        &__before, &__after{
            width: 50%;
            height: 100%;
        }

        &__wrap-slider {
            height: 45rem;
            max-height: 45rem;
        }
    }
}

@media screen and (min-width: 1200px) {
    .surgery-results {
        max-width: 1106px;
        padding: 5rem 0 3rem;
        margin: 0 auto;
        &__pointer.pointer__wrap {
            left: 0;
        }
        &__wrap {
            display: flex;
            flex-direction: row;
        }
        &__h2 {
            padding: 2rem;
        }
        &__list {
            justify-content: flex-start;
        }
        &__item {
            padding: 0.6rem 1.2rem 0.6rem 0;
        }
        &__link {
            width: 21.5rem;
            height: 18.1rem;
            padding: 0 1rem;
        }

        &__h2{
            padding-left: 3.3rem;
        }

        &__h2,
        &__link-text {
            font-size: 2.4rem;
            line-height: 28px;
            letter-spacing: 0.67px;
        }
        &__left,
        &__right {
            position: relative;
        }
        &__left {
            flex: 0 0 49%;
            z-index: 4;
        }
        &__right {
            flex: 0 0 57.5%;
            margin: 0;
            left: -7rem;
            width: calc(100% + 7rem);
            z-index: 3;
            max-height: 70rem;
            overflow: hidden;
        }
        &__slider {
            max-height: 70rem;
            &.slick-slider.slick-vertical {
                .slick-slide {
                    height: 70rem;
                    max-height: 70rem;
                }
            }
            .slick-dots {
                top: 0;
                height: 70rem;
                width: 2rem;
                right: 4.7rem;
                flex-direction: column;
                align-items: center;
                li {
                    margin: 1rem 0;
                }
                [type='button']:hover {
                    cursor: pointer;
                }
            }
            .slick-arrow {
                position: absolute;
                right: 5.3rem;
                z-index: 5;
                border: none;
                color: transparent; // height: 10.1rem;
                // width: 0.9rem;
                background-color: transparent;
                background-size: 101px auto;
                background-repeat: no-repeat;
                display: inline-block;
                background: url('../images/surgery-results/arrow-vertical_sprite.png') no-repeat;
                overflow: hidden;
                text-indent: -9999px;
                text-align: left;
                width: 9px;
                height: 101px;
                outline: 1px solid transparent;
            }
            .slick-arrow.slick-next {
                bottom: 6rem;
                background-position: -0px -101px;
                cursor: pointer;
                &:hover {
                    background-position: -9px -101px;
                }
            }
            .slick-arrow.slick-prev {
                top: 6rem;
                background-position: -0px -0px;
                cursor: pointer;
                &:hover {
                    background-position: -9px -0px;
                }
            }
            &.slick-initialized .slick-slide {
                display: block;
            }
        }
        &__before, &__after{
            width: 100%;
            height: 50%;
        }

        &__wrap-slider{
            height: 70rem;
            max-height: 70rem;
        }
    }
}