*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    font-size: 62.5% //1 rem = 10px; 10px/16px = 62.5%
}

body {
    box-sizing: border-box;
}

::selection {
    background: #010101;
    color: #fff;
    text-shadow: none;
}

.text-block__formatting *{
    color: $p-color-1;
    font-family: $light;
    font-size: 1.2rem;
    font-weight: 300;
    line-height: 2.8rem;
    letter-spacing: 0.33px;
    padding-bottom: 3rem;
    list-style-type: none;

    li::before{
        content: ""; 
        color: $p-color-1;
        display: inline-block; 
        width: 1rem;
    }

    &:last-child{
        padding-bottom: 0;
    }
}

.mob-none {
    display: none;
}
.tab-none{
    display: none;
}

@media screen and (min-width:768px){
    .mob-none {
        display: block;
    }
}
@media screen and (min-width:1200px){
    .tab-none {
        display: block;
    }

    .text-block__formatting, .text-block__formatting *{
        letter-spacing: 0.39px;
        font-size: 1.4rem;
        padding-bottom: 3rem;
    }
}