.main-head__school {
    height: auto;
}

.main-head__img {
    background-size: cover;
}

.title__school {
        color: $white-1;
        font-size: 1.8rem;
        font-weight: 300;
        line-height: 2.8rem;
        text-transform: uppercase;
        font-family: $light;
        padding: 2.5rem 0;
    }


.subtitle {
    &__outline {
        left: 0;
    }
    &__school {
        position: relative;
        padding: 4rem 0;
        h2 {
            padding-left: 3.3rem;
            color: $white-1;
            font-family: $light;
            font-size: 1.8rem;
            font-weight: 400;
            line-height: 2.8rem;
            text-transform: uppercase;
        }
    }
}

// .school {
// }
.container {
    &_lower {
        position: relative;
        z-index: 1;
        width: 100%;
        height: auto;
    }
}

.lower {
    &_lines {
        padding: 8rem 0 0 0;
        justify-content: flex-start;
    }
}

.container {
    &_upper {
        position: relative;
        z-index: 3;
    }
}

.school {
    &__wrap {
        display: flex;
        width: 100%;
        flex-wrap: wrap;
        height: auto;
        position: relative;
    }
    &__contant {
        width: 100%;
        height: auto;
    }
    &__container {
        width: 100%;
        height: auto;
    }
    &__thirdtitle {
        left: 0;
        position: relative;
        margin: 4.5rem 0 2.3rem 0;
        h3 {
            color: $header-color;
            font-family: $light;
            font-size: 1.8rem;
            font-weight: 400;
            line-height: 2.8rem;
            text-transform: uppercase;
            padding-left: 4rem;
        }
    }
}

.second-wraper {
    padding: 0 1.5rem 0 1.5rem;
    width: 100%;
    height: auto;
}

.calendar {
    width: 100%;
    height: auto;
    box-shadow: 0px 0.5px 5px 0.5px;
    &__month {
        width: 100%;
        height: 30rem;
        background: pink;
    }
    &__cursors {
        width: 100%;
        height: 6.4rem;
        background: blue;
    }
}

.school__pointer {
    left: 0;
}

.courses {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto; // padding: 0 1.5rem 0 1.5rem;
    &__item {
        width: 100%;
        height: auto;
        padding: 0 0 4.2rem 0;
        display: flex;
        flex-direction: column;
        &-mask {
            width: 100%;
            height: 34.2rem;
            position: relative;
            &:first-child {
                padding-top: 2rem;
            }
            &:last-child {
                padding-bottom: 6.1rem;
            }
        }
    }
}

.courses__item {
        &_new {
            &-info {
                h3 {
                    padding: 3.2rem 0 4rem 0;
                    color: $video-overlay-grad;
                    font-family: $regular;
                    font-size: 1.2rem;
                    font-weight: 400;
                    line-height: 2.6rem;
                    text-transform: uppercase;
                    letter-spacing: 0.33px;
                }
                p {
                    padding-bottom: 3rem;
                    color: $video-overlay-grad;
                    font-family: $regular;
                    font-size: 1.2rem;
                    font-weight: 400;
                    line-height: 2.6rem;
                    text-transform: uppercase;
                    letter-spacing: 0.33px;
                }
            }
        }
        &__btn {
            width: 12.2rem;
            margin: 0 auto;
        }
    }


.individual {
    width: 100%;
    height: 16rem;
    padding-top: 4.3rem;
    background-image: $section-bg-grad;
    margin-top: 4rem;
    padding-left: 1.5rem;
    &__subclause {
        position: relative;
        width: 100%;
        height: auto;
        padding-left: 4rem;
        h2 {
            color: $white-1;
            font-family: $regular;
            font-size: 1.8rem;
            font-weight: 400;
            line-height: 2.6rem;
            text-transform: uppercase;
            letter-spacing: 0.5px;
            a {
                position: relative;
            }
        }
    }
}

.internship {
    width: 100%;
    height: auto;
    padding: 0 1.5rem 0 1.5rem;
    margin-top: -3.7rem;
    &__inner {
        display: flex;
        flex-direction: column;
        height: auto;
        flex-wrap: nowrap;
        align-items: center;
        padding: 1.5rem;
        background: $white-1;
        box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.1);
    }
        &__img {
            width: 100%;
            margin: 0 auto;
            height: auto;
            background-size: cover;
            padding: 0;
            &-link {
                width: 100%;
            }
            &-text {
                color: $white-1;
                font-family: $light;
                font-size: 1.2rem;
                font-weight: 400;
                line-height: 1.8rem;
                letter-spacing: 0.33px;
            }
            &:first-child {
                padding-bottom: 1rem;
            }
        }
    }


.release_container {
        &__dates {
            width: 100%;
            height: auto;
            padding: 0 1.5rem 0 1.5rem;
            margin-top: 4.5rem;
            &-activ {
                position: relative;
                padding-left: 3rem;
                position: relative;
                span {
                    color: $dark-blue-1;
                    font-family: $medium;
                    font-size: 1.4rem;
                    font-weight: 500;
                    letter-spacing: 0.39px;
                    padding-top: 2.8rem;
                }
            }
        }
    }


.release__none::before {
    height: 0;
}

.release_container {
        &__dates {
            &-noactiv {
                width: 50%;
                padding-top: 3rem;
                span {
                    opacity: 0.6;
                    color: $header-color;
                    font-family: $light;
                    font-size: 1.4rem;
                    font-weight: 400;
                    letter-spacing: 0.39px;
                }
            }
        }
    }


.container__school {
        &_galery {
            padding-top: 3rem;
            width: 100%;
            height: auto;
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: space-around;
        }
    }


.school_galery {
        width: 29.3rem;
        height: 24.2rem;
        box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.1);
    }


.school_galery {
        &__cursors {
            width: 100%;
            height: 6rem;
            background: greenyellow;
        }
    }


