//--------------------------------------------------------------------------------------------//
@media screen and (min-width: 768px) {
    .style__tab {
        position: static;
        max-width: 100%; // height: 40rem;
    }
    .title__school {
            text-align: center;
            padding: 3.5rem 0;
            font-size: 2.4rem;
        }

    .subtitle__school {
            position: relative;
            &__outline {
                left: 0;
            }
            h2 {
                font-size: 2.4rem;
                padding-left: 6rem;
            }
            &::after {
                content: '';
                position: absolute;
                display: block;
                width: 10rem;
                height: 10px;
                background-repeat: no-repeat;
                top: 50%;
                right: 0;
                background-image: url('../images/recommends-page/arrow-horiz_right-light-lg.svg');
                cursor: pointer;
            }
            &::before {
                content: '';
                position: absolute;
                display: block;
                width: 10rem;
                height: 10px;
                background-repeat: no-repeat;
                top: 50%;
                left: 50%;
                background-image: url('../images/recommends-page/arrow-horiz_left-light-lg.svg');
                transform: translate(-50%, -50%);
                cursor: pointer;
            }
        }
    
    .calendar {
        width: 72.2rem;
        height: auto;
        margin: 0 auto;
        position: relative;
        &__month {
            margin-top: -12.8rem;
        }
    }
    
    .insirt {
        height: 40rem;
    }
    .school__container {
        padding: 0 1.5rem 0 1.5rem;
        &__thirdtitle {
            margin: 7.7rem 0 7.1rem 0;
        }
        &__thirdtitle h3 {
            font-size: 2.4rem;
            font-weight: 400;
        }
    }
    .courses__item {
        display: flex;
        justify-content: flex-start;
        padding: 0 0 6rem 0;
        &__btn {
            align-self: start;
            margin: 0;
        }
        &-mask {
            height: 34.6rem;
            width: 26rem;
        }
        &_new {
            width: 43.5rem;
            height: auto;
            display: flex;
            flex-direction: column;
        }
        &:last-child {
            padding-bottom: 0;
        }
        &:nth-child(2n) {
            flex-direction: row-reverse;
        }
        &:nth-child(2n+1) {
            flex-direction: row;
        }
    }
    .courses__item {
        &_new-info {
            padding-top: 1rem;
            
            height: 28rem;
            h3 {
                padding: 0;
                font-size: 1.4rem;
                font-weight: 400;
                line-height: 2.8rem;
            }
            p {
                font-size: 1.4rem;
                font-weight: 400;
                line-height: 2.8rem;
            }
        }
    }
       
    .individual {
        padding-top: 6rem;
        margin-top: 15rem;
        height: 28rem;
        &__subclause {
            padding-left: 4rem;
            span {
                font-size: 1.8rem;
                font-weight: 400;
                line-height: 2.8rem;
            }
        }
    }

    .internship {
        padding: 0 2.2rem 0 2.2rem;
        margin-top: -15rem;
        &__inner {
            top: 0;
            height: 90rem;
            background: white;
            padding-top: 4rem;
        }
            &__img {
                width: 100%;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                padding: 0 3rem 0 3rem;
                &-link {
                    width: 100%;
                    height: 40rem;
                }
                &-text {
                    font-size: 1.8rem;
                    font-weight: 400;
                    line-height: 2.8rem;
                }
            }
        }
    
    
    .release__none {
        left: 0;
    }
    .release_container{
        &__dates {
        padding-left: 2.2rem;
        padding-right: 2.2rem;
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        &-activ{ 
            span {
            font-size: 1.8rem;
            font-weight: 500;
            }
        }
    }
}
    
    .school_projects{
        &__gallery {
        width: 100%;
        height: 40rem;
    }
}
    
    .release_container{
        &__dates-noactiv {
        height: 50px;
        padding-top: 0;
        span {
            font-size: 1.8rem;
            font-weight: 400;
            line-height: 2.8rem;
        }
    }
}
   
    .courses-left {
        padding-left: 5rem;
    }
    .courses-right {
        padding-right: 5rem;
    }
} //---------------------------------------------------------------------------------------//
@media screen and (min-width: 1200px) {
    main {
        overflow: hidden;
    }
    .school__scroll {
        top: -50rem;
    }
    .school__main-head {
        height: 110vh;
        position: relative;
        z-index: 1;
    }
    .style__tab {
        position: absolute;
        bottom: auto;
        height: 100%;
        transform: translateY(0);
    }
    .style__tab-block::before {
        background-image: none;
    }
    .insirt {
        height: 100%;
    }
    .school__wrap {
        padding: 0;
        position: relative;
        z-index: 2;
    }
    .school__wrap::before {
        display: none;
    }
    .title__school {
        text-align: left;
        position: relative;
        display: inline-block;
        max-width: 60%;
        font-size: 3.6rem;
        line-height: 48px;
        top: 50%;
        padding-top: 0;
        transform: translateY(-50%);
        &::before {
            content: '';
            height: 23.5rem;
            ;
            right: 1rem;
            top: 40%;
            transform: translateY(-50%);
            position: absolute;
            width: 100vw;
            background-image: $section-bg-grad;
            z-index: 1;
        }
        span {
            line-height: 4.8rem;
            position: relative;
            z-index: 2;
        }
    }
    
    
    .subtitle {
        max-width: 100%;
        width: 100%;
        position: relative;
        &__school {
            padding: 5.8rem 0 5.4rem 0;
            position: relative;
        }
        &::before {
            content: '';
            width: 200vw;
            height: 40rem;
            background-image: $section-bg-grad;
            top: 0;
            left: -100%;
            position: absolute;
            z-index: 0;
        }
    }
    
    
    .school__container {
        padding: 0;
        width: 1106px;
        margin: 0 auto;
        &__thirdtitle {
            margin: 8rem 0 8rem 0;
        }
    }

    
    .calendar {
        width: 1106px;
        height: auto;
        margin: 0 auto;
        position: relative;
        &__month {
            margin-top: 3.5rem;
        }
    }
    .second-wraper {
        padding: 0;
    }
    .school__pointer {
        left: 0;
    }
    .courses__item {
        padding-bottom: 10rem;
        justify-content: space-between;
        &_new {
            width: 58.3rem;
        }
        &_new__info p,
    h3 {
        font-size: 1.8rem;
        font-weight: 400;
        line-height: 3.4rem;
    }
    &-mask {
        width: 38rem;
        height: 50rem;
    }
    }
    .courses-left {
        padding: 5rem 0 0 0;
        width: 66.3rem;
    }
    .courses-right {
        padding: 5rem 0 0 0;
        width: 66.3rem;
    }
    .internship{
        padding: 0;
        &__inner {
        height: auto;
        width: 1106px;
        margin: 0 auto;
        padding: 4.2rem;
        }
        &__img {
            flex-direction: row;
            justify-content: space-between;
            padding: 0;
            &-link {
               width: calc(50% - 1.5rem);
                margin-bottom: 0;
            }
            &:first-child {
                padding-bottom: 0;
            }
        }
    }

    .individual {
        padding-top: 5.5rem;
        padding-left: 0;
        &__subclause {
            margin: 0 auto;
            width: 1106px;
        }
    }
    .releas {
        width: 1106px;
        margin: 0 auto;
    }
    .school_btn {
        background: $white-1;
    }
}